/* ==============================================================
   Change Local
   style declarations for changeLocal.xhtml
   ==============================================================*/
/* BLOCK */
.change-local {
	display: none;
	position: absolute;
	top: 3.2rem;
	left: 50%;
	margin-left: -2.35rem;
	padding-top: .1rem;
	background-color: @blue;
	box-shadow: 0px 6px 10px rgba(0,0,0,.25);
	z-index: 2000;
	&:before {
		content: "";
		position: absolute;
		display: block;
		width: 0;
		height: 0;
		top: -1.2rem;
		left: 50%;
		margin-left: -.6rem;
		border-width: .6rem;
		border-style: solid;
		border-color: transparent transparent @blue transparent;
	}
	ul {}
	li {
		display: list-item;
		float: none;
		margin: 0;
		margin-bottom: .1rem;
	}
	a {
		padding: .3rem 1.4rem;
		&:hover,
		&:active,
		&:focus {
			background-color: @hoverblue;			
		}
		&.activeLang {
			background-color: @hoverblue;
		}
	}
	img {}
}

/* BREAKPOINT FOR LARGE DESKTOPS */
@media screen and (max-width: @largeBreakpoint) {}

/* BREAKPOINT FOR DESKTOPS */
@media screen and (max-width: @mediumBreakpoint) {}

/* BREAKPOINT FOR TABLETS */
@media screen and (max-width: @smallBreakpoint) {}

/* BREAKPOINT FOR SMARTPHONES */
@media screen and (max-width: @xSmallBreakpoint) {}